import React, { useState } from 'react'

const Contact = () => {
    const [data, setdata] =useState({
        fullname:"",
        phone :"",
        email:"",
        msg:"",
    });
    const InputEvent =( event) =>{
        const {name, value } = event.target;
        setdata((preVal) =>{
            return {
                ...preVal,
                [name]: value,
            };
        });
    };
    const formSubmit =(e) =>{
        e.preventDefault();
        alert(`My name is ${data.fullname}. My mobile number is${data.phone},Here is what I want to say${data.msg}`);
    };
    return (
        <>
            <div className='my-5'>
                <h2 className='text-center'>Contact us now</h2>
            </div>
            <div className='container contact_div'>
                <div className='row'>
                    <div className='col-md-6 col-10 mx-auto'>
                        <form onSubmit={formSubmit}>
                            <div class="mb-3">
                                <label for="exampleFormControlInput1" class="form-label">Full Name</label>
                                <input type="text" class="form-control" id="exampleFormControlInput1" placeholder="Enter your Name" name='fullname' value={data.fullname} onChange={InputEvent} required />
                            </div>
                            <div class="mb-3">
                                <label for="exampleFormControlInput1" class="form-label">Phone No.</label>
                                <input type="number" class="form-control" id="exampleFormControlInput1" placeholder="Mobile Number"name='phone' value={data.phone} onChange={InputEvent} required/>
                            </div>
                            <div class="mb-3">
                                <label for="exampleFormControlInput1" class="form-label">Email address</label>
                                <input type="email" class="form-control" id="exampleFormControlInput1" placeholder="name@example.com"name='email' value={data.email} onChange={InputEvent} required/>
                            </div>
                            <div class="mb-3">
                                <label for="exampleFormControlTextarea1" class="form-label">Message</label>
                                <textarea class="form-control" id="exampleFormControlTextarea1" rows="3"name='msg' value={data.msg} onChange={InputEvent}></textarea>
                            </div>
                            <div class="col-10 ">
                                <button class="btn btn-outline-primary" type="submit">Submit form</button>
                            </div>
                        </form>
                    </div>
                </div>
            </div>
        </>

    );
};

export default Contact;